.company__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1222px;
  margin: 0 auto;
  margin-bottom: 120px;

  @include vp-1023 {
    max-width: 770px;
    justify-content: center;
    margin-bottom: 80px;
  }

  @include vp-767 {
    max-width: 270px;
  }
}

.company img {
  @include vp-767 {
    display: none;
  }
}

.company__content {
  max-width: 970px;
  color: $color-black-pearl;

  @include vp-1023 {
    max-width: 495px;
    margin-left: 29px;
  }

  @include vp-767 {
    margin-left: 0;
  }
}

.company h2 {
  margin: 0;
  padding: 0;
  font-family: $ff-roboto;
  font-weight: 400;
  font-size: 36px;
  line-height: 51px;
  margin-bottom: 15px;

  @include vp-767 {
    font-size: 28px;
    line-height: 39px;
  }
}

.company__description {
  margin-bottom: 20px;
}

.company__text {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;

  &--about-us {
    margin-bottom: 25px;
  }

  &--clients {
    @include vp-767 {
      display: none;
    }
  }

  @include vp-767 {
    font-size: 14px;
    line-height: 19px;
  }
}

.company__button {
  min-width: 180px;
  min-height: 45px;
  padding: 0;
  background-color: transparent;
  border: 1px solid $color-deep-sky-blue;
  border-radius: 3px;
  font-family: $ff-roboto;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-deep-sky-blue;
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: $color-deep-sky-blue;
    color: $color-alice-blue;
  }

  &:active {
    opacity: 0.7;
  }
}
